.roughin-logo {
  background-image: url(https://roydude.github.io/images/roughin-logo-white.png);
  background-size: cover;
  position: fixed;
  width: 32px;
  height: 47px;
  margin: 30px 0px 0px 30px;
}

.footer-card {
  position: fixed;
  bottom: 0px;
  width: 100%;
  /* height: fit-content; */
  height: 154px;
  padding: 20px;
  box-sizing: border-box;

  color: white;
  background: rgba(0, 0, 0, 0.75);
  font-weight: bold;
  display: flex;
  flex-direction: column;

  /* 가로축 - 가운데 정렬 */
  /* justify-content: center; */
  
  /* 세로축 - 가운데 정렬 */ 
  align-items: center;

  transition-duration: 0.5s;
}

.footer-card-open {
  position: fixed;
  bottom: 0px;
  width: 100%;
  /* height: fit-content; */
  height: 400px;
  padding: 20px;
  box-sizing: border-box;

  color: white;
  background: rgba(0, 0, 0, 0.75);
  font-weight: bold;
  display: flex;
  flex-direction: column;

  /* 가로축 - 가운데 정렬 */
  /* justify-content: center; */
  
  /* 세로축 - 가운데 정렬 */ 
  align-items: center;
  
  transition-duration: 0.5s;
}

.icon {
  background-image: url(https://roydude.github.io/images/icon/Up%202.png);

  /* 버튼 위치 조정 */
  position: relative;
  width: 24px;
  height: 24px;

  display: block;
  justify-content: center;
  align-items: center;
  
  transition: 0.5s;

  /* background: #ffffff; */
}

.contentsText {
  overflow: hidden;
  position: relative;
  width: 100%;
  /* height: 80px; */
  height: fit-content;
  margin: 10px 20px 0px 20px;
  justify-content: center;
}

.menuTitle {
  /* position: absolute; */
  /* width: 77px;
  height: 26px;
  left: 20px;
  top: 723px; */

  font-family: 'Pretendard';
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 26px;
  /* identical to box height, or 118% */

  letter-spacing: 0.25px;

  color: #FFFFFF;
}

.menuDesc {
  /* position: absolute; */
  /* width: 314px;
  height: 44px;
  left: 20px;
  top: 759px; */
  
  font-family: 'Pretendard';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  /* or 138% */
  
  margin-top: 10px;

  letter-spacing: 0.44px;
  
  color: #FFFFFF;
}

button {
  cursor: pointer;
  /*버튼 default 스타일 제거*/
  -webkit-appearance: none; 
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: none;
  outline: none;
}

